import { CBadge, CButton, CCard, CCardBody, CDataTable, CInput } from '@coreui/react'
import {React} from 'react'
import { useHistory } from 'react-router-dom'

const Beranda = () =>{
    const history = useHistory()
    return(
        <>
        <CCard>
            <CCardBody>
                <div className="d-flex flex-row justify-content-center mb-4">
                    <h5>Beranda Permohonan</h5>
                </div>
                <div className="d-flex flex-row justify-content-end mb-3">
                    <div className="mr-2">
                        <CInput/>                        
                    </div>
                    <div className="mr-2">
                        <CButton className="btn btn-light"><i class="fas fa-search"></i></CButton>
                    </div>
                    <div>
                        <CButton onClick={()=>history.push('/ppbj/new')} className="btn btn-success"><i class="fas fa-plus"></i></CButton>
                    </div>                    
                </div>                
                <CDataTable
                addTableClasses="tablebiru"
                fields={[
                    {key:'no', label:'Nomor'},
                    {key:'noaju', label:'Nomor Pengajuan'},
                    {key:'noppbj', label:'Nomor PPBJ'},
                    {key:'tglbpj', label:'Tanggal BPJ'},
                    {key:'refppbj', label:'Referensi PPBJ'},
                    {key:'lawantrx', label:'Lawan Transaksi'},
                    {key:'objtrx', label:'Objek Transaksi'},
                    {key:'status', label:'Status'},
                    {key:'aksi', label:'Aksi'},                    
                ]}
                items={[
                    {
                        no:'1',
                        noaju:'901017D9867A3',
                        noppbj:'10000042',
                        tglbpj:'14-07-2021',
                        refppbj:'829374837263',
                        lawantrx:'120,000,000',
                        objtrx:'BKP Berwujud',
                        status:'Pengajuan'
                    },
                    {
                        no:'2',
                        noaju:'901017DASJD123',
                        noppbj:'100001243',
                        tglbpj:'21-07-2021',
                        refppbj:'8293712413',
                        lawantrx:'450,000,000',                        
                        objtrx:'JKP',
                        status:'Selesai'
                    },
                    {
                        no:'3',
                        noaju:'9010171867A3',
                        noppbj:'100000124',
                        tglbpj:'24-05-2021',
                        refppbj:'8293748124123',
                        lawantrx:'12,000,000',                        
                        objtrx:'BKP Berwujud',
                        status:'Selesai'
                    }, 
                    {
                        no:'4',
                        noaju:'9010171ASDA3',
                        noppbj:'1000003424',
                        tglbpj:'24-01-2021',
                        refppbj:'8293748112344',
                        lawantrx:'235,000,000',                        
                        objtrx:'BKP Berwujud',
                        status:'Pembatalan'
                    }                                                           
                ]}
                scopedSlots={{
                    'aksi':
                    (item, index) =>{
                        return(
                            <td>
                                <div className="d-flex flex-row">
                                    <div className="mr-1">
                                    <CButton style={{backgroundColor:'#02275d', color:'white'}} className="btn btn-sm"><i class="fas fa-eye"></i></CButton>                                        
                                    </div>
                                    <div className="mr-1">
                                    <CButton style={{backgroundColor:'#517fa4', color:'white'}} className="btn btn-sm"><i class="far fa-play-circle"></i></CButton>                                        
                                    </div>  
                                    <div className="mr-1">
                                    <CButton style={{backgroundColor:'#f76c6b', color:'white'}} className="btn btn-sm"><i class="fas fa-ban"></i></CButton>                                        
                                    </div>   
                                    <div className="mr-1">
                                    <CButton style={{backgroundColor:'#c8ced3', color:'white'}} className="btn btn-sm"><i class="fas fa-cloud-download-alt"></i></CButton>                                        
                                    </div>                                                                                                          
                                </div>
                            </td>
                        )
                    },
                    'status':
                    (item, index)=>{
                        let color 
                        if(item.status == "Pengajuan"){
                            color = "#64c2de"
                        } else if (item.status == "Selesai"){
                            color  = "#4dbd74"
                        } else if (item.status == "Pembatalan"){
                            color = "#2f353a"
                        }
                        return(
                            <td>
                                <CBadge style={{backgroundColor:`${color}`, color:'white'}}>{item.status}</CBadge>
                            </td>
                        )
                    }
                }}
                />
            </CCardBody>
        </CCard>
        </>
    )
}

export default Beranda